import 'dayjs/locale/pt-br';
import React, { PureComponent } from 'react';
import _concat from 'lodash/concat';
import _size from 'lodash/size';
import _get from 'lodash/get';
import _times from 'lodash/times';
import _isEmpty from 'lodash/isEmpty';
import _uniqBy from 'lodash/uniqBy';
import dayjs from 'dayjs';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import Button from '@material-ui/core/Button';
import FormControl from '@material-ui/core/FormControl';
import PropTypes from 'prop-types';
import { compose, withState } from 'recompose';
import Header from '../../components/Header';
import AlertBox from '../../components/AlertBox';
import withRequest from '../../utils/with-request';
import VisitorsService from '../../services/visitors';
import VisitorsTable from './VisitorsTable';
import styles from './styles';
import config from '../../config';

const LAST_MONTHS = 12;
const PER_PAGE = 20;

class Visitors extends PureComponent {
  state = {
    alertMsg: '',
    alertType: '',
    showAlert: false
  }

  clearAlert = () => this.setState({ showAlert: false, alertMsg: '' });

  renderAlertBox = () => {
    const { alertMsg, alertType, showAlert } = this.state;
    if (showAlert) {
      return <AlertBox message={alertMsg} type={alertType} onClose={this.clearAlert} />;
    }
    return null;
  }

  onSelectMonth = async (e) => {
    const {
      setHideSeeMore, setMonth, setVisitors,
      setDisableSeeMore, visitorsFetch, setPage
    } = this.props;
    try {
      setDisableSeeMore(true);
      setPage(0);
      setMonth(e.target.value);
      const visitorsData = await visitorsFetch(true);
      const newVisitors = _uniqBy(_get(visitorsData, 'visitors', []), 'id');
      setVisitors(newVisitors);
      setDisableSeeMore(false);
      setHideSeeMore(_isEmpty(newVisitors));
    } catch {
      setDisableSeeMore(false);
      setHideSeeMore(true);
    }
  }

  onSeeMore = async () => {
    const {
      visitors, setVisitors, page, setPage,
      setHideSeeMore, setDisableSeeMore, visitorsFetch
    } = this.props;
    try {
      setDisableSeeMore(true);
      setPage(page + 1);
      const visitorsData = await visitorsFetch(false);
      const newVisitors = _get(visitorsData, 'visitors', []);
      setVisitors(_uniqBy(_concat(visitors, newVisitors), 'id'));
      setDisableSeeMore(false);
      setHideSeeMore(_isEmpty(newVisitors));
    } catch {
      setDisableSeeMore(false);
      setVisitors(visitors);
      setHideSeeMore(true);
    }
  }

  render() {
    const {
      visitors, visitorsLoading, visitorsError,
      disableSeeMore, classes, month, hideSeeMore, totalVisitors
    } = this.props;
    const error = visitorsError;
    const currentMonth = dayjs().month();
    const canShowMore = Boolean(
      !visitorsLoading && !((_size(visitors) || 1) === totalVisitors)
    );
    return (
      <div className={classes.root}>
        <Header alertBox={this.renderAlertBox()} error={error} />
        <section>
          <div className={classes.container}>
            <div className={classes.headerView}>
              <aside className={classes.headerTitle}>
                <Typography variant="h4"><b>{totalVisitors ? `Visitantes (${totalVisitors})` : 'Visitantes' }</b></Typography>
                <FormControl className={classes.inputField} fullWidth>
                  <Select
                    fullWidth
                    className={classes.button}
                    value={month}
                    onChange={this.onSelectMonth}
                  >
                    {
                      _times(LAST_MONTHS, (t) => (
                        <MenuItem key={currentMonth - t} value={currentMonth - t}>
                          {dayjs().locale(config.locale).subtract(t, 'month').format('MMMM YYYY')}
                        </MenuItem>
                      ))
                    }
                  </Select>
                </FormControl>
              </aside>
            </div>
          </div>
          <div className={classes.container}>
            <VisitorsTable visitors={visitors} loading={visitorsLoading} />
            {
              canShowMore && !hideSeeMore &&
                <div className={classes.buttonContainer}>
                  <Button
                    className={classes.showMoreButton}
                    onClick={this.onSeeMore}
                    size="large"
                    variant="contained"
                    color="primary"
                    disabled={disableSeeMore}
                  >
                    {disableSeeMore ? 'Carregando' : 'Ver Mais'}
                  </Button>
                </div>
            }
          </div>
        </section>
      </div>
    );
  }
}

Visitors.propTypes = {
  classes: PropTypes.object,
  visitorsLoading: PropTypes.bool,
  visitorsError: PropTypes.object,
  visitorsFetch: PropTypes.func,
  visitors: PropTypes.array,
  setVisitors: PropTypes.func,
  setMonth: PropTypes.func,
  totalVisitors: PropTypes.number,
  month: PropTypes.number,
  setPage: PropTypes.func,
  page: PropTypes.number,
  setDisableSeeMore: PropTypes.func,
  disableSeeMore: PropTypes.bool,
  setHideSeeMore: PropTypes.func,
  hideSeeMore: PropTypes.bool
};

const withVisitors = withRequest(VisitorsService.getVisitors, {
  name: 'visitors',
  options: ({ page, month, setVisitors, setHideSeeMore, setTotalVisitors }) => ({
    variables: { page, perPage: PER_PAGE, month: month + 1 },
    onRequestFinished: (data) => {
      const visitors = _uniqBy(_get(data, 'visitors', []), 'id');
      const totalVisitors = _get(data, 'meta.total', 0);
      setVisitors(visitors);
      setHideSeeMore(_isEmpty(visitors));
      setTotalVisitors(totalVisitors);
    }
  })
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('totalVisitors', 'setTotalVisitors', 0),
  withState('visitors', 'setVisitors', []),
  withState('hideSeeMore', 'setHideSeeMore', false),
  withState('disableSeeMore', 'setDisableSeeMore', false),
  withState('page', 'setPage', 0),
  withState('month', 'setMonth', dayjs().month()),
  withVisitors
)(Visitors);
