import React, { memo } from 'react';
import PropTypes from 'prop-types';
import dayjs from 'dayjs';
import _get from 'lodash/get';
import { TableCell, TableRow } from '@material-ui/core';
import styles from './styles';

const DELIVERY_TYPES = {
  package: 'Encomenda',
  food: 'Comida'
};

const DELIVERY_METHODS = {
  member_receive: 'Ocupante desceu',
  expedition_receive: 'Expedição recebeu',
  office_delivery: 'Entregue no escritório',
  expedition_delivery: 'Expedição entregou'
};

const DeliveriesItemRow = memo(({ index, delivery }) => {
  const memberFullName = `${_get(delivery, 'member.name')} ${_get(delivery, 'member.lastname')}`;
  const memberEmail = _get(delivery, 'member.email');
  const companyName = _get(delivery, 'member.company.name');
  const deliveryDate = dayjs(_get(delivery, 'created_at')).format('DD/MM/YY HH:mm');
  const deliveryMethod = _get(delivery, 'delivery_method');
  const deliveryType = _get(delivery, 'delivery_type');
  return (
    <TableRow style={index % 2 ? styles.bgWhite : styles.bgGray}>
      <TableCell style={styles.tableSmallCell}>
        {deliveryDate}
      </TableCell>
      <TableCell style={styles.tableSmallCell}>
        {DELIVERY_METHODS[deliveryMethod]}
      </TableCell>
      <TableCell style={styles.tableSmallCell}>
        {DELIVERY_TYPES[deliveryType]}
      </TableCell>
      <TableCell style={styles.tableMediumCell}>
        {memberFullName}
        <br />
        {companyName}
        <br />
        <small>{memberEmail}</small>
      </TableCell>
    </TableRow>
  );
});

DeliveriesItemRow.propTypes = {
  index: PropTypes.number,
  delivery: PropTypes.object
};

export default DeliveriesItemRow;
