/* global document */
import React, { Suspense, PureComponent } from 'react';
import ReactDOM from 'react-dom';
import { HashRouter, Route, Switch } from 'react-router-dom';
import _get from 'lodash/get';
import _map from 'lodash/map';
import CssBaseline from '@material-ui/core/CssBaseline';
import { MuiThemeProvider } from '@material-ui/core/styles';
import EnterpriseUserService from './services/enterprise-user';
import Session from './utils/session';
import withTracker from './utils/with-tracker';
import Loading from './components/Loading';
import theme from './theme';
import config from './config';
import Routes from './routes';
import * as Pages from './pages';

class App extends PureComponent {
  state = {
    loading: true
  }

  componentDidMount() {
    this.setState({ loading: true }, async () => {
      if (config.isiOS) {
        document.querySelector('link[rel="manifest"]').setAttribute('rel', 'no-on-ios');
      }
      try {
        const enterpriseUserResponse = await EnterpriseUserService.getEnterpriseUser();
        const enterpriseUser = _get(enterpriseUserResponse, 'enterprise_user');
        Session.setUserData(enterpriseUser);
      } catch {
        Session.setUserData(null);
      } finally {
        this.setState({ loading: false });
      }
    });
  }

  render() {
    const { loading } = this.state;
    return (
      <MuiThemeProvider theme={theme}>
        <CssBaseline />
        <Suspense fallback={<Loading hasRefreshMsg />}>
          {
            loading ?
              <Loading hasRefreshMsg />
              :
              <HashRouter>
                <Switch>
                  {
                    _map(Routes, (r, key) => {
                      if (r.submenus) {
                        return _map(r.submenus, (sr, skey) => (
                          <Route
                            key={`${r.path}${sr.path}`}
                            exact={sr.exact}
                            path={`${r.path}${sr.path}`}
                            component={withTracker(Pages[skey])}
                          />
                        ));
                      }
                      return (
                        <Route
                          key={r.path}
                          exact={r.exact}
                          path={r.path}
                          component={withTracker(Pages[key])}
                        />
                      );
                    })
                  }
                </Switch>
              </HashRouter>
          }
        </Suspense>
      </MuiThemeProvider>
    );
  }
}

ReactDOM.render(<App />, document.querySelector('main'));
