import React, { memo } from 'react';
import PropTypes from 'prop-types';
import dayjs from 'dayjs';
import _get from 'lodash/get';
import { TableCell, TableRow } from '@material-ui/core';
import styles from './styles';

const MembersItemRow = memo(({ index, member }) => {
  const memberFullName = `${_get(member, 'name')} ${_get(member, 'lastname')}`;
  const memberEmail = _get(member, 'email');
  const memberPhone = _get(member, 'phone');
  const memberCreatedAt = dayjs(_get(member, 'created_at')).format('DD/MM/YY HH:mm');
  return (
    <TableRow style={index % 2 ? styles.bgWhite : styles.bgGray}>
      <TableCell style={styles.tableSmallCell}>
        {memberFullName}
      </TableCell>
      <TableCell style={styles.tableSmallCell}>
        {memberEmail}
      </TableCell>
      <TableCell style={styles.tableSmallCell}>
        {memberPhone}
      </TableCell>
      <TableCell style={styles.tableMediumCell}>
        {memberCreatedAt}
      </TableCell>
    </TableRow>
  );
});

MembersItemRow.propTypes = {
  index: PropTypes.number,
  member: PropTypes.object
};

export default MembersItemRow;
