import React, { memo } from 'react';
import PropTypes from 'prop-types';
import _size from 'lodash/size';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Loading from '../../../components/Loading';
import DeliveriesItemRow from '../DeliveriesItemRow';
import styles from './styles';

const DeliveriesTable = memo((props) => {
  const { deliveries, loading } = props;
  return (
    <div style={styles.container}>
      <Paper style={styles.paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell style={styles.tableCell}>DATA</TableCell>
              <TableCell style={styles.tableCell}>ENTREGA</TableCell>
              <TableCell style={styles.tableCell}>TIPO</TableCell>
              <TableCell style={styles.tableCell}>OCUPANTE</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {
              loading ?
                <TableRow>
                  <TableCell colSpan={4} align="center">
                    <Loading />
                  </TableCell>
                </TableRow>
                :
                (
                  _size(deliveries) ?
                    (deliveries.map((delivery, i) => {
                      return (
                        <DeliveriesItemRow
                          index={i}
                          key={delivery.id}
                          delivery={delivery}
                          loading={loading}
                        />
                      );
                    }))
                    :
                    <TableRow>
                      <TableCell style={styles.empty} colSpan={4} align="center">
                        Nenhuma entrega registrada
                      </TableCell>
                    </TableRow>
                )
            }
          </TableBody>
        </Table>
      </Paper>
    </div>
  );
});

DeliveriesTable.propTypes = {
  deliveries: PropTypes.array,
  loading: PropTypes.bool
};

export default DeliveriesTable;
