import React, { memo } from 'react';
import PropTypes from 'prop-types';
import _size from 'lodash/size';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Loading from '../../../components/Loading';
import MembersItemRow from '../MembersItemRow';
import styles from './styles';

const MembersTable = memo((props) => {
  const { members, loading } = props;
  return (
    <div style={styles.container}>
      <Paper style={styles.paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell style={styles.tableCell}>NOME</TableCell>
              <TableCell style={styles.tableCell}>EMAIL</TableCell>
              <TableCell style={styles.tableCell}>CELULAR</TableCell>
              <TableCell style={styles.tableCell}>DATA DE CADASTRO</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {
              loading ?
                <TableRow>
                  <TableCell colSpan={4} align="center">
                    <Loading />
                  </TableCell>
                </TableRow>
                :
                (
                  _size(members) ?
                    (members.map((member, i) => {
                      return (
                        <MembersItemRow
                          index={i}
                          key={member.id}
                          member={member}
                          loading={loading}
                        />
                      );
                    }))
                    :
                    <TableRow>
                      <TableCell style={styles.empty} colSpan={4} align="center">
                        Nenhum usuário cadastrado
                      </TableCell>
                    </TableRow>
                )
            }
          </TableBody>
        </Table>
      </Paper>
    </div>
  );
});

MembersTable.propTypes = {
  members: PropTypes.array,
  loading: PropTypes.bool
};

export default MembersTable;
