import React, { memo } from 'react';
import PropTypes from 'prop-types';
import _size from 'lodash/size';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Loading from '../../../components/Loading';
import VisitorsItemRow from '../VisitorsItemRow';
import styles from './styles';

const VisitorsTable = memo((props) => {
  const { visitors, loading } = props;
  return (
    <div style={styles.container}>
      <Paper style={styles.paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell style={styles.tableCell}>DATA VISITA</TableCell>
              <TableCell style={styles.tableCell}>CHECK-IN</TableCell>
              <TableCell style={styles.tableCell}>STATUS</TableCell>
              <TableCell style={styles.tableCell}>VISITANTE</TableCell>
              <TableCell style={styles.tableCell}>ANFITRIÃO</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {
              loading ?
                <TableRow>
                  <TableCell colSpan={5} align="center">
                    <Loading />
                  </TableCell>
                </TableRow>
                :
                (
                  _size(visitors) ?
                    (visitors.map((visitor, i) => {
                      return (
                        <VisitorsItemRow
                          index={i}
                          key={visitor.id}
                          visitor={visitor}
                          loading={loading}
                        />
                      );
                    }))
                    :
                    <TableRow>
                      <TableCell style={styles.empty} colSpan={5} align="center">
                        Nenhum visitante registrado
                      </TableCell>
                    </TableRow>
                )
            }
          </TableBody>
        </Table>
      </Paper>
    </div>
  );
});

VisitorsTable.propTypes = {
  visitors: PropTypes.array,
  loading: PropTypes.bool
};

export default VisitorsTable;
