import React, { PureComponent } from 'react';
import _get from 'lodash/get';

const withRequest = (dataSource, { name = null, init = true, options = () => ({}) }) => Wrapped => (
  class Request extends PureComponent {
    state = { loading: true, reloadedMsg: '', error: null, data: null };

    loadingProperty = name ? `${name}Loading` : 'loading';

    reloadedMsgProperty = name ? `${name}ReloadedMsg` : 'reloadedMsg';

    errorProperty = name ? `${name}Error` : 'error';

    dataProperty = name ? `${name}Data` : 'data';

    clearProperty = name ? `${name}Clear` : 'clear';

    fetchProperty = name ? `${name}Fetch` : 'fetch';

    componentDidMount() {
      if (init) {
        this.fetch(true);
      } else {
        this.setState({ loading: false, data: {} });
      }
    }

    getOption = (optionName) => {
      const resolvedOptions = options(this.props);
      return _get(resolvedOptions, optionName);
    };

    loadDataSource = async (done) => {
      const variables = this.getOption('variables');
      const onRequestFinished = this.getOption('onRequestFinished');
      const onRequestError = this.getOption('onRequestError');
      try {
        const data = await dataSource(variables);
        if (onRequestFinished) await onRequestFinished(data);
        return done(null, data);
      } catch (error) {
        if (onRequestError) await onRequestError(error);
        return done(error, null);
      }
    }

    fetch = (loading = false, reloadedMsg = '', done) => {
      return new Promise((resolve, reject) => {
        this.setState({ loading }, () => {
          this.loadDataSource((error, data) => {
            this.setState({ data, error, loading: false, reloadedMsg }, () => {
              if (error) {
                reject(error);
              } else {
                resolve(data);
              }
              return done && done();
            });
          });
        });
      });
    }

    clear = done => this.setState({ data: null }, done);

    render() {
      const propsWithState = {
        ...this.props,
        [this.errorProperty]: this.state.error,
        [this.dataProperty]: this.state.data,
        [this.loadingProperty]: this.state.loading,
        [this.reloadedMsgProperty]: this.state.reloadedMsg,
        [this.fetchProperty]: this.fetch,
        [this.clearProperty]: this.clear
      };
      return <Wrapped {...propsWithState} />;
    }
  }
);

export default withRequest;
