import React, { PureComponent } from 'react';
import _size from 'lodash/size';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import { compose } from 'recompose';
import Header from '../../components/Header';
import AlertBox from '../../components/AlertBox';
import withRequest from '../../utils/with-request';
import MembersService from '../../services/members';
import MembersTable from './MembersTable';
import styles from './styles';

class Members extends PureComponent {
  state = {
    alertMsg: '',
    alertType: '',
    showAlert: false
  }

  clearAlert = () => this.setState({ showAlert: false, alertMsg: '' });

  renderAlertBox = () => {
    const { alertMsg, alertType, showAlert } = this.state;
    if (showAlert) {
      return <AlertBox message={alertMsg} type={alertType} onClose={this.clearAlert} />;
    }
    return null;
  }

  render() {
    const { members, membersLoading, membersError, classes } = this.props;
    const error = membersError;
    const totalMembers = _size(members);
    return (
      <div className={classes.root}>
        <Header alertBox={this.renderAlertBox()} error={error} />
        <section>
          <div className={classes.container}>
            <div className={classes.headerView}>
              <aside className={classes.headerTitle}>
                <Typography variant="h4">
                  <b>{totalMembers ? `Usuários (${totalMembers})` : 'Usuários' }</b>
                </Typography>
              </aside>
            </div>
          </div>
          <div className={classes.container}>
            <MembersTable members={members} loading={membersLoading} />
          </div>
        </section>
      </div>
    );
  }
}

Members.propTypes = {
  classes: PropTypes.object,
  membersLoading: PropTypes.bool,
  membersError: PropTypes.object,
  members: PropTypes.array
};

const withMembers = withRequest(MembersService.getMembers, {
  name: 'members'
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withMembers
)(Members);
